<template>
  <div class="multivariate-result">
    <div class="return-page" v-if="sourceChannel === 'multivariate-view'">
      <a href @click.prevent.stop="returnPrev">重新分析</a>
    </div>
    <div class="parm-des" v-if="sourceChannel === 'multivariate-view'">
      <span>{{parmDes}}</span>
    </div>
    <el-card>
      <div slot="header">
        <span>数据统计结果</span>
      </div>
      <div class="wrap-edit-level clear" v-show="showdatatable">
        <!-- 三个变量table -->
        <div class="three-varList-container clear" v-if="showThreeVarsTable">
          <div ref="edit-three-container" class="box-edit-level fl" style="margin-bottom: 20px;">
            <table class="active-table">
              <thead>
                <tr v-if="threeThead.length">
                  <th colspan="2" rowspan="2">{{cellTitle}}</th>
                  <th :colspan="threeThead.length">{{threeHeadTitle}}</th>
                </tr>
                <tr>
                  <th v-for="(item, index) in threeThead" :key="index">{{item}}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in threeTbody" :key="index">
                  <td :rowspan="threeTbody.length" v-if="index==0">{{threeBodyTitle}}</td>
                  <td
                    v-for="(v,i) in item.children"
                    :data-x="v.x"
                    :data-y="v.y"
                    :class="v.rate"
                    @click="setThreeColor($event, v, index, i)"
                    :key="i"
                  >{{v.value}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="box-btn-edit fr">
            <ul class="btn-edit-list clear">
              <li>
                <span>评级</span>
                <b>{{cellTitle}}</b>
                <strong>占比</strong>
                <el-popover
                  class="el-popper__custom el-popper__level"
                  placement="right"
                  width="400"
                  trigger="hover"
                >
                  <div>
                    <div class="el-popover__title">
                      <img src="../assets/icon-brow.png" alt>蜂鸟智能
                    </div>
                    <p>评级编辑功能：仅当指标类型为贷后时，对统计结果提供编辑分级功能，最多9级，分别为：R、R2 、R1、 1、2、3、4、5、6，每级分别对应着不同颜色，先选中分级按钮，在选择中左侧的统计结果，则格子会自动填充上等级的颜色，同时会相应的展示选中等级所对应的统计结果值选中擦除，可擦除选中格子等级颜色。</p>
                  </div>
                  <img src="../assets/bird-2.gif" alt slot="reference">
                </el-popover>
              </li>
              <li
                v-for="(item,index) in level3"
                :level="item.level"
                @click="setThreeCurrent(index)"
                :key="index"
              >
                <el-popover
                  :class="{cur:threeVarsColorIndex===index}"
                  :style="{backgroundColor:item.bgcolor}"
                  placement="left"
                  width="400"
                  v-show="item.cells.length"
                  trigger="hover"
                >
                  <div>
                    <div class="el-popover__title">
                      <img src="../assets/icon-brow.png" alt>蜂鸟智能
                    </div>
                    <p v-for="(cell, i) in item.cells" :key="i">{{cell}}</p>
                  </div>
                  <em slot="reference">{{item.level}}</em>
                </el-popover>
                <span
                  v-show="item.cells.length == 0"
                  :class="{cur:threeVarsColorIndex===index}"
                  :style="{backgroundColor:item.bgcolor}"
                >{{item.level}}</span>
                <b v-if="item.level !== '擦除'">{{item.value}}</b>
                <strong v-if="item.level !== '擦除'">{{item.percent}}</strong>
              </li>
            </ul>
          </div>
        </div>
        <div class="two-varList-container" v-if="showThreeVarsTable && cellArr.length">
          <div ref="edit-two-container" class="box-edit-level fl">
            <table class="active-table">
              <thead>
                <tr v-if="twoThead.length">
                  <th colspan="2" rowspan="2">{{cellTitle}}</th>
                  <th :colspan="twoThead.length">{{twoHeadTitle}}</th>
                </tr>
                <tr>
                  <th v-for="(item, index) in twoThead" :key="index">{{item}}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in twoTbody" :key="index">
                  <td :rowspan="twoTbody.length" v-if="index==0">{{twoBodyTitle}}</td>
                  <td
                    v-for="(v,i) in item.children"
                    :data-x="v.x"
                    :data-y="v.y"
                    :class="v.rate"
                    @click="setTwoColor($event, v, index, i)"
                    :key="i"
                  >{{v.value}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="box-btn-edit fr">
            <ul class="btn-edit-list clear">
              <li>
                <span>评级</span>
                <b>{{cellTitle}}</b>
                <strong>占比</strong>
                <el-popover
                  class="el-popper__custom el-popper__level"
                  placement="right"
                  width="400"
                  trigger="hover"
                >
                  <div>
                    <div class="el-popover__title">
                      <img src="../assets/icon-brow.png" alt>蜂鸟智能
                    </div>
                    <p>评级编辑功能，仅当指标类型为贷后时，对统计结果提供编辑分级功能，最多9级，分别为：R，R2，R1，1，2，3，4，5，6，每级分别对应着不同颜色</p>
                    <p>先选中分级按钮，在选中左侧的统计结果，则格子会自动填充上等级颜色，同时会相应的展示选中等级所对应的统计结果值</p>
                    <p>选中擦除，可擦除选中格子等级颜色</p>
                  </div>
                  <img src="../assets/bird-2.gif" alt slot="reference">
                </el-popover>
              </li>
              <li
                v-for="(item,index) in level2"
                :level="item.level"
                @click="setTwoCurrent(index)"
                :key="index"
              >
                <el-popover
                  :class="{cur:twoVarsColorIndex===index}"
                  :style="{backgroundColor:item.bgcolor}"
                  placement="left"
                  width="400"
                  v-show="item.cells.length"
                  trigger="hover"
                >
                  <div>
                    <div class="el-popover__title">
                      <img src="../assets/icon-brow.png" alt>蜂鸟智能
                    </div>
                    <p v-for="(cell, i) in item.cells" :key="i">{{cell}}</p>
                  </div>
                  <em slot="reference">{{item.level}}</em>
                </el-popover>
                <span
                  v-show="item.cells.length == 0"
                  :class="{cur:twoVarsColorIndex===index}"
                  :style="{backgroundColor:item.bgcolor}"
                >{{item.level}}</span>
                <b v-if="item.level !== '擦除'">{{item.value}}</b>
                <strong v-if="item.level !== '擦除'">{{item.percent}}</strong>
              </li>
            </ul>
          </div>
        </div>
        <!-- 两个变量table -->
        <div class="three-varList-container clear" v-if="showTwoVarsTable">
          <div ref="edit-two-container" class="box-edit-level fl">
            <table class="active-table">
              <thead>
                <tr v-if="twoThead.length">
                  <th colspan="2" rowspan="2">{{cellTitle}}</th>
                  <th :colspan="twoThead.length">{{twoHeadTitle}}</th>
                </tr>
                <tr>
                  <th v-for="(item, index) in twoThead" :key="index">{{item}}</th>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in twoTbody" :key="index">
                  <td :rowspan="twoTbody.length" v-if="index==0">{{twoBodyTitle}}</td>
                  <td
                    v-for="(v,i) in item.children"
                    :data-x="v.x"
                    :data-y="v.y"
                    :class="v.rate"
                    @click="setTwoColor($event, v, index, i)"
                    :key="i"
                  >{{v.value}}</td>
                </tr>
              </tbody>
            </table>
          </div>
          <div class="box-btn-edit fr">
            <ul class="btn-edit-list clear">
              <li>
                <span>评级</span>
                <b>{{cellTitle}}</b>
                <strong>占比</strong>
                <el-popover
                  class="el-popper__custom el-popper__level"
                  placement="right"
                  width="400"
                  trigger="hover"
                >
                  <div>
                    <div class="el-popover__title">
                      <img src="../assets/icon-brow.png" alt>蜂鸟智能
                    </div>
                    <p>评级编辑功能，仅当指标类型为贷后时，对统计结果提供编辑分级功能，最多9级，分别为：R，R2，R1，1，2，3，4，5，6，每级分别对应着不同颜色</p>
                    <p>先选中分级按钮，在选中左侧的统计结果，则格子会自动填充上等级颜色，同时会相应的展示选中等级所对应的统计结果值</p>
                    <p>选中擦除，可擦除选中格子等级颜色</p>
                  </div>
                  <img src="../assets/bird-2.gif" alt slot="reference">
                </el-popover>
              </li>
              <li
                v-for="(item,index) in level2"
                :level="item.level"
                @click="setTwoCurrent(index)"
                :key="index"
              >
                <el-popover
                  :class="{cur:twoVarsColorIndex===index}"
                  :style="{backgroundColor:item.bgcolor}"
                  placement="left"
                  width="400"
                  v-show="item.cells.length"
                  trigger="hover"
                >
                  <div>
                    <div class="el-popover__title">
                      <img src="../assets/icon-brow.png" alt>蜂鸟智能
                    </div>
                    <p v-for="(cell, i) in item.cells" :key="i">{{cell}}</p>
                  </div>
                  <em slot="reference">{{item.level}}</em>
                </el-popover>
                <span
                  v-show="item.cells.length == 0"
                  :class="{cur:twoVarsColorIndex===index}"
                  :style="{backgroundColor:item.bgcolor}"
                >{{item.level}}</span>
                <b v-if="item.level !== '擦除'">{{item.value}}</b>
                <strong v-if="item.level !== '擦除'">{{item.percent}}</strong>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="nodata el-table__empty-block" v-if="!showdatatable">暂无数据</div>
    </el-card>
    <div
      v-show="showdatatable"
      class="box-save-export"
      v-if="sourceChannel === 'multivariate-view'"
    >
      <el-button type="primary" @click.stop.prevent="saveResult">保存</el-button>
      <el-button type="primary" v-if="showsource" @click.stop.prevent="exportSource">导出数据源</el-button>
      <el-button type="primary" @click.stop.prevent="export2Excel">导出图表</el-button>
    </div>

    <filename
      @save="saveFile"
      :init-visible.sync="showfilesave"
      v-if="sourceChannel === 'multivariate-view'"
    ></filename>
  </div>
</template>

<script>
import filename from '@/components/filename';
import mixinsVar from '@/mixins/mixins-var.js';
import mixinsMultivar from '@/mixins/mixins-multivar.js';
import {getVarList} from '@/api/sample.js';
import {saveAnalysis, queryAnalysisDetail} from '@/api/analysis.js';
import {exportDecision} from '@/api/decision.js';
import {
  getOnePrefilter,
  getOverdueList,
  getSectionContent,
  getLevel,
  getOneVarSelect
} from '@/assets/scripts/getSomeSetting.js';
import {exportSpecialExcel, json2Excel} from '@/assets/scripts/table2excel';

export default {
  name: 'multivariate-result',
  components: {
    filename
  },
  props: {
    recordId: {
      type: String
    },
    sourceChannel: {
      type: String,
      required: true
    }
  },
  mixins: [mixinsVar, mixinsMultivar],
  data() {
    return {
      level3: getLevel(),
      level2: getLevel(),
      // 默认设置选中第一个颜色
      threeVarsColorIndex: 0,
      twoVarsColorIndex: 0,
      showdatatable: false,
      showThreeVarsTable: false,
      showTwoVarsTable: false,
      threeThead: [],
      threeTbody: [],
      twoThead: [],
      twoTbody: [],
      cellArr: [],
      cellTitle: '',
      twoBodyTitle: '',
      twoHeadTitle: '',
      threeBodyTitle: '',
      threeHeadTitle: '',
      decisionDates: [],
      loanDates: [],
      indicator: '',
      varList: [],
      prefilter: [getOnePrefilter()],
      varselectlist: [getOneVarSelect()],
      sectioncontent: getSectionContent(),
      overduelist: getOverdueList(),
      showfilesave: false
    };
  },
  computed: {
    showsource() {
      // 是否显示导出数据源按钮
      return this.indicator === 'AFTER_LOAN' && !this.recordId;
    },
    parmDes() {
      const keyArr = this.varselectlist.map(varselect => {
        let theOne = this.varList.find(item => item.value === varselect.fieldName) || {};
        return theOne.label;
      });
      return `${this.postdata.indexName} ${keyArr.join('\\')} ${this.cellTitle}`;
    }
  },
  created() {
    this.postdata = {};
    this.filesaveid = '';
    this.getAnalysisResult()
      .then(result => {
        this.result = result;
        this.postdata = result.postdata;
        if (result.threeTbody.length > 0 || result.twoTbody.length > 0) {
          this.showdatatable = true;
          this.threeThead = result.threeThead;
          this.threeTbody = result.threeTbody;
          this.twoThead = result.twoThead;
          this.twoTbody = result.twoTbody;
          this.stashThead = result.stashThead;
          this.stashTbody = result.stashTbody;
        }
        this.initFields(this.postdata);
        this.getVars([this.rcProductName, this.moduleType, this.samplename], true, {}, true);
      })
      .catch(err => console.log(err));
  },
  mounted() {
    this.threeLevelColor = this.level3[this.threeVarsColorIndex].truecolor;
    this.twoLevelColor = this.level2[this.twoVarsColorIndex].truecolor;
  },
  methods: {
    returnPrev() {
      this.$store.commit('UPDATE_VAR_TYPE', 'multivariate');
      this.$store.commit('UPDATE_ANALYSISRESULT', {
        key: 'multivariate',
        value: this.result
      });
      if (this.filesaveid) {
        this.$store.commit('UPDATE_ANALYSISRESULT', {
          key: 'file',
          value: {
            name: this.filename,
            id: this.filesaveid
          }
        });
        this.$router.push({
          path: '/variate',
          query: {
            recordid: this.filesaveid
          }
        });
      } else {
        this.$router.push('/variate');
      }
    },
    getAnalysisResult() {
      return new Promise((resolve, reject) => {
        if (this.recordId) {
          // 获取保存的分析结果
          queryAnalysisDetail(this.recordId)
            .then(result => {
              if (result.result == null) {
                this.$message.warning('此历史记录不存在');
                reject('此历史记录不存在');
                return;
              }
              this.filename = result.result.name;
              this.filesaveid = result.result.id;
              const data = JSON.parse(result.result.result);
              resolve(data);
            })
            .catch(err => reject(err));
        } else {
          const result = this.$store.state.analysisResult.multivariate;
          const file = this.$store.state.analysisResult.file;
          this.filename = file.name;
          this.filesaveid = file.id;
          resolve(result);
        }
      });
    },
    getVars(val, isMain = true, target, isInit = false) {
      this.rcProductName = val[0];
      this.moduleType = val[1];
      this.samplename = val[2];
      this.initSample = val;
      getVarList(this.samplename)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!result.result.length) {
              return;
            }
            this.varList = result.result.map(({key, vbname, fieldtype, transmittype}) => {
              return {
                label: `${vbname}${fieldtype === 'float' ? '（连续）' : '（离散）'}`,
                value: key,
                fieldtype,
                transmittype
              };
            });
            if (isMain && isInit) {
              this.initFieldsAboutVar(this.postdata);
              this.getTableTitle();
              if (this.varselectlist.length > 2) {
                this.showThreeVarsTable = true;
                this.cellArr = this.twoThead.map(item =>
                  item
                    .split('-')
                    .slice(0, 2)
                    .join('-')
                );
                this.setLevelValue('threeTbody', 'level3');
                this.setLevelValue('twoTbody', 'level2');
              } else {
                this.showTwoVarsTable = true;
                this.setLevelValue('twoTbody', 'level2');
              }
            }
          }
        })
        .catch(err => console.log(err));
    },
    getTableTitle() {
      let arr = [];
      for (let item of this.varselectlist) {
        let obj = this.varList.find(one => one.value === item.fieldName);
        if (obj) {
          arr.push(obj.label);
        }
      }
      let subTitle = '';
      if (this.indicator === 'BEFORE_LOAN') {
        if (this.beforeway === 'avg') {
          let theOne = this.varList.find(item => item.value === this.avgvar) || {};
          subTitle = `贷前_平均值_${theOne.label}`;
        } else {
          subTitle = `贷前_计数`;
        }
        // subTitle = this.ispercent ? `${subTitle}（%）` : subTitle;
      } else if (this.indicator === 'AFTER_LOAN') {
        let theOne = this.overduelist.find(item => item.value === this.overdue) || {};
        subTitle = `贷后_${theOne.text}_${this.aftertype === 'peoples' ? '人数' : '金额'}_${
          this.aftercount === 'rate' ? '逾期率' : '逾期量'
        }`;
      }
      this.cellTitle = subTitle;
      if (this.varselectlist.length > 2) {
        this.threeBodyTitle = arr[0];
        this.threeHeadTitle = arr[1];
        this.twoBodyTitle = arr[2];
        this.twoHeadTitle = '评级';
      } else {
        this.twoBodyTitle = arr[0];
        this.twoHeadTitle = arr[1];
      }
    },
    setThreeCurrent(index) {
      this.threeVarsColorIndex = index;
      this.threeLevelColor = this.level3[this.threeVarsColorIndex].truecolor;
    },
    setTwoCurrent(index) {
      this.twoVarsColorIndex = index;
      this.twoLevelColor = this.level2[this.twoVarsColorIndex].truecolor;
    },
    setThreeColor(event, item, col, row) {
      // 点击元素的评级和当前评级一致
      if (
        row === 0 ||
        event.target.className === this.threeLevelColor ||
        (!event.target.className && this.threeLevelColor === 'transparent')
      ) {
        return;
      }
      let flag = '';
      if (event.target.classList.length) {
        if (this.threeLevelColor === 'transparent') {
          flag = 'delete';
        } else {
          flag = 'update';
        }
      } else {
        flag = 'add';
      }
      const preRate = item.rate;
      event.target.className = '';
      if (flag === 'delete') {
        item.rate = '';
      } else {
        event.target.classList.add(this.threeLevelColor);
        item.rate = this.threeLevelColor;
      }
      // 动态生成二级表格
      this.createTwoTable(item, flag, col, row);
      this.setLevelValue('threeTbody', 'level3');
    },
    setTwoColor(event, item, col, row) {
      // 点击元素的评级和当前评级一致
      if (
        row === 0 ||
        event.target.className === this.twoLevelColor ||
        (!event.target.className && this.twoLevelColor === 'transparent')
      ) {
        return;
      }
      const preRate = item.rate;
      event.target.className = '';
      if (this.twoLevelColor === 'transparent') {
        item.rate = '';
      } else {
        event.target.classList.add(this.twoLevelColor);
        // 写入样式数据
        item.rate = this.twoLevelColor;
      }
      this.setLevelValue('twoTbody', 'level2');
    },
    setLevelValue(tbody, level) {
      const source = this[tbody];
      const target = this[level];
      let count = 0;
      target.forEach(item => {
        item.denominator = 0;
        item.numerator = 0;
        item.value = 0;
        item.percent = 0;
        item.cells = [];
      });
      for (let row of source) {
        for (let one of row.children) {
          if (this.ispercent || this.aftercount === 'rate') {
            count += Number.isNaN(Number(one.denominator)) ? 0 : Number(one.denominator);
          } else {
            count += Number.isNaN(Number(one.value)) ? 0 : Number(one.value);
          }
          if (one.rate) {
            const theLevel = target.find(item => item.truecolor === one.rate);
            if (theLevel) {
              let value = 0;
              let denominator = 0;
              let numerator = 0;
              // 百分比
              if (this.ispercent || this.aftercount === 'rate') {
                numerator = Number.isNaN(Number(one.numerator)) ? 0 : Number(one.numerator);
                denominator = Number.isNaN(Number(one.denominator)) ? 0 : Number(one.denominator);
                theLevel.numerator += numerator;
                theLevel.denominator += denominator;
              } else {
                value = Number.isNaN(Number(one.value)) ? 0 : Number(one.value);
                theLevel.value += value;
              }
              let desc = '';
              if (level === 'level3') {
                desc = `${this.threeBodyTitle}[${one.x}], ${this.threeHeadTitle}[${one.y}]`;
              } else if (level === 'level2') {
                if (this.showThreeVarsTable) {
                  desc = `${this.threeBodyTitle}[${one.pX}], ${this.threeHeadTitle}[${one.pY}], ${this.twoBodyTitle}[${
                    one.x
                  }]`;
                } else {
                  desc = `${this.twoBodyTitle}[${one.x}], ${this.twoHeadTitle}[${one.y}]`;
                }
              }
              theLevel.cells.push(desc);
            }
          }
        }
      }
      target.forEach(item => {
        if (this.ispercent || this.aftercount === 'rate') {
          item.value = item.denominator ? `${((item.numerator * 100) / item.denominator).toFixed(2)}%` : 0;
          item.percent = item.denominator ? `${((item.denominator * 100) / count).toFixed(2)}%` : 0;
        } else {
          item.value = Number.parseInt(item.value);
          item.percent = item.value ? `${((item.value * 100) / count).toFixed(2)}%` : 0;
        }
      });
    },
    createTwoTable(item, flag, col, row) {
      const level = this.level3[this.threeVarsColorIndex].level;
      const id = item.id;
      // 维护this.stashTbody数组，用来追踪用户点击了那些单元格
      if (flag === 'add') {
        this.cellArr.push(id);
        this.stashThead.push(`${level}`);
        for (let i = 0; i < item.children.length; i++) {
          let one = item.children[i];
          this.stashTbody[i] = this.stashTbody[i] || {};
          this.stashTbody[i].children = Array.isArray(this.stashTbody[i].children)
            ? this.stashTbody[i].children
            : [
                {
                  x: one.key,
                  y: level,
                  pX: item.x,
                  pY: item.y,
                  key: one.key,
                  value: one.key,
                  abnormal: false,
                  numerator: one.numerator,
                  denominator: one.denominator,
                  rate: one.rate
                }
              ];
          this.stashTbody[i].children.push({
            x: one.key,
            y: level,
            pX: item.x,
            pY: item.y,
            key: one.key,
            value: one.value === null ? 'N/A' : one.value,
            abnormal: one.abnormal,
            numerator: one.numerator,
            denominator: one.denominator,
            rate: one.rate
          });
        }
      } else if (flag === 'update') {
        let j = this.cellArr.indexOf(id);
        this.cellArr.splice(j, 1, id);
        this.stashThead.splice(j, 1, `${level}`);
        for (let i = 0; i < item.children.length; i++) {
          let one = item.children[i];
          this.stashTbody[i].children.splice(j + 1, 1, {
            x: one.key,
            y: level,
            pX: item.x,
            pY: item.y,
            key: one.key,
            value: one.value === null ? 'N/A' : one.value,
            abnormal: one.abnormal,
            numerator: one.numerator,
            denominator: one.denominator,
            rate: one.rate
          });
        }
      } else if (flag === 'delete') {
        let j = this.cellArr.indexOf(id);
        this.cellArr.splice(j, 1);
        this.stashThead.splice(j, 1);
        for (let i = 0; i < item.children.length; i++) {
          this.stashTbody[i].children.splice(j + 1, 1);
        }
      }
      this.twoTbody = [];
      this.twoThead = [];
      for (let i = 0; i < this.stashTbody.length; i++) {
        this.twoTbody[i] = {};
        this.twoTbody[i].children = this.stashTbody[i].children.slice(0, 1);
      }
      for (let j = 0; j < this.stashThead.length; j++) {
        let m = this.twoThead.indexOf(this.stashThead[j]) + 1;
        if (m > 0) {
          for (let i = 0; i < this.stashTbody.length; i++) {
            if (this.twoTbody[i].children[m].value === 'N/A') {
              if (this.stashTbody[i].children[j + 1] && this.stashTbody[i].children[j + 1].value !== 'N/A') {
                this.twoTbody[i].children[m].value = this.stashTbody[i].children[j + 1].value;
              }
            } else {
              if (this.stashTbody[i].children[j + 1] && this.stashTbody[i].children[j + 1].value !== 'N/A') {
                this.twoTbody[i].children[m].value =
                  Number(this.twoTbody[i].children[m].value) + Number(this.stashTbody[i].children[j + 1].value);
              }
            }
          }
        } else {
          this.twoThead.push(this.stashThead[j]);
          for (let i = 0; i < this.stashTbody.length; i++) {
            this.twoTbody[i].children.push(Object.assign({}, this.stashTbody[i].children[j + 1]));
          }
        }
      }
    },
    export2Excel() {
      if (this.showThreeVarsTable) {
        this.downloadExcel(this.threeTbody, this.threeThead, this.threeBodyTitle, this.threeHeadTitle);
        if (this.cellArr.length) {
          setTimeout(() => {
            this.downloadExcel(this.twoTbody, this.twoThead, this.twoBodyTitle, this.twoHeadTitle);
          }, 1000);
        }
      } else if (this.showTwoVarsTable) {
        this.downloadExcel(this.twoTbody, this.twoThead, this.twoBodyTitle, this.twoHeadTitle);
      }
    },
    exportSource2Excel(head, body) {
      const bodydata = body.map(function(v1) {
        return v1.map(function(v2) {
          if (v2 === null) {
            v2 = 'N/A';
          }
          return v2;
        });
      });
      json2Excel(bodydata, `${this.samplename}_${new Date().toLocaleString()}.xlsx`);
    },
    exportSource() {
      if (!this.checkSource()) {
        console.log('======导出数据源 表单检查不通过======');
        return;
      }
      let postdata = this.getPostData();
      postdata.loanType = this.indicator;

      if (this.indicator === 'BEFORE_LOAN') {
        //选择贷前
        if (this.beforeway === 'count') {
          //选择计数
          postdata.resultValueType = 'COUNT';
          postdata.resultField = '';
        }
        if (this.beforeway === 'avg') {
          //选择平均值
          postdata.resultValueType = 'FIELD_AVG';
          postdata.resultField = this.avgvar;
        }
        postdata.resultNeedPercent = this.ispercent;
      }
      if (this.indicator === 'AFTER_LOAN') {
        //选择贷后
        if (this.aftertype === 'peoples') {
          //类型选择人数
          postdata.resultValueType = 'COUNT';
          postdata.resultField = '';
        }
        if (this.aftertype === 'amount') {
          //类型选择金额
          postdata.resultValueType = 'FIELD_SUM';
          postdata.resultField = 'LoanCapital';
        }
        if (this.aftercount === 'count') {
          //统计值选择逾期量
          postdata.resultNeedPercent = false;
        }
        if (this.aftercount === 'rate') {
          //统计值选择逾期率
          postdata.resultNeedPercent = true;
        }
        let overduedata = this.overdue.split('-');
        postdata.afterLoanKPI = overduedata[0];
        postdata.overdueDays = overduedata[1];
      }
      exportDecision(postdata)
        .then(result => {
          if (result.result.length == 0) {
            this.$message.warning('暂无数据');
            return;
          }
          this.$message.success('导出数据源成功');
          this.exportSource2Excel(result.result);
        })
        .catch(err => console.log(err));
    },
    checkSource() {
      console.log('=========开始检查表单=========');
      if (this.samplename.length === 0) {
        this.$message.warning('请选择样本');
        return;
      }
      if (this.indicator.length === 0) {
        this.$message.warning('请选择指标类型');
        return;
      }
      if (this.indicator === 'AFTER_LOAN' && this.overdue.length === 0) {
        this.$message.warning('请选择逾期指标');
        return;
      }

      return true;
    },
    downloadExcel(tbody, thead, tbodyTitle = '', theadTitle = '') {
      const data = [];
      for (let i = 0; i < tbody.length + 2; i++) {
        data[i] = [];
        for (let j = 0; j < tbody[0].children.length + 1; j++) {
          data[i][j] = null;
          if (i > 1) {
            if (j > 0) {
              data[i][j] = tbody[i - 2].children[j - 1].value;
            }
          } else if (i === 1 && j > 1) {
            // 添加threeThead
            data[i][j] = thead[j - 2];
          }
        }
      }
      // 添加cellTitle
      data[0][0] = this.cellTitle;
      // 添加twoHeadTitle
      data[0][2] = theadTitle;
      // 添加twoBodyTitle
      data[2][0] = tbodyTitle;
      const format = [
        // 设置A1-C1的单元格合并
        {s: {r: 0, c: 0}, e: {r: 1, c: 1}},
        {s: {r: 0, c: 2}, e: {r: 0, c: data[0].length - 1}},
        {s: {r: 2, c: 0}, e: {r: data.length - 1, c: 0}}
      ];
      exportSpecialExcel(data, format, `${this.samplename}_${new Date().toLocaleString()}.xlsx`);
    },
    saveFile(name) {
      let data = {};
      const keyArr = this.varselectlist.map(varselect => {
        let theOne = this.varList.find(item => item.value === varselect.fieldName) || {};
        return theOne.label;
      });
      let parmDes = `${this.postdata.indexName} ${keyArr.join('\\')} ${this.cellTitle}`;
      data.parmDes = parmDes;
      data.name = name;
      data.type = 1;
      data.id = this.filesaveid;
      const postdata = JSON.parse(JSON.stringify(this.postdata));
      for (let item of postdata.statisticConditionList) {
        for (let assistVar of item.assistVarList) {
          delete assistVar.varList;
        }
      }
      data.result = JSON.stringify({
        postdata,
        threeThead: this.threeThead,
        threeTbody: this.threeTbody,
        twoThead: this.twoThead,
        twoTbody: this.twoTbody,
        stashThead: this.stashThead,
        stashTbody: this.stashTbody
      });

      saveAnalysis(data)
        .then(result => {
          this.$message.success('保存成功');
          this.filename = data.name;
          this.filesaveid = result.result;
        })
        .catch(err => console.log(err));
    },
    saveResult() {
      if (!this.filesaveid) {
        this.showfilesave = true;
        return;
      }
      this.saveFile(this.filename);
    }
  }
};
</script>

